<template>
    <div>
        <div v-if="authorized">
            <h2>Edit Post</h2>
            <CreatePost
                :edit="true"
                :initialTitle="title"
                :initialText="text"
                :initialStatus="status"
                :postId="$route.params.id"
            ></CreatePost>
            <ErrorList></ErrorList>
        </div>
        <p v-else>Whoops! Not authorized to edit this post.</p>
    </div>
</template>
<script>
import CreatePost from "@/components/CreatePost";
import ErrorList from "@/components/ErrorList";
import axios from "axios";

export default {
    name: "CreatePostPage",
    data() {
        return {
            title: "",
            text: "",
            status: "",
            authorId: ""
        };
    },
    components: {
        CreatePost,
        ErrorList
    },
    async created() {
        this.$store.commit("setErrorMessage", "");
        this.$store.commit("setErrors", []);
        try {
            const response = await axios.get(
                `/api/post/${this.$route.params.id}`
            );
            this.authorId = response.data.results[0].author._id.toString();
            this.title = response.data.results[0].title;
            this.text = response.data.results[0].text;
            this.status = response.data.results[0].status;
        } catch (err) {
            console.log(err);
        }
    },
    computed: {
        loggedIn: function() {
            return !(this.$store.getters.jwt === "");
        },
        authorized: function() {
            if (this.loggedIn)
                return this.$store.getters.jwtData._id === this.authorId;
            else return false;
        }
    }
};
</script>
<style scoped></style>
