<template>
    <form @submit.prevent="post">
        <label for="title">Title: </label>
        <input
            type="text"
            required
            v-model="title"
            placeholder="Put title here..."
            name="title"
            id="title"
        />
        <label for="text">Comment</label>
        <textarea name="text" id="text" rows="5" v-model="text">
            Put text here...
        </textarea>
        <label for="author">Author</label>
        <input
            type="text"
            required
            v-model="author"
            placeholder="Put your name here..."
            name="author"
            id="author"
        />
        <button type="submit">Post Comment</button>
    </form></template
>
<script>
import axios from "axios";

export default {
    name: "CommentForm",
    data() {
        return {
            title: "",
            text: "",
            author: !(this.$store.getters.jwt === "")
                ? this.$store.getters.jwtData.username
                : ""
        };
    },
    methods: {
        post() {
            axios({
                url: `/api/post/${this.$route.params.id}/addcomment`,
                data: {
                    title: this.title,
                    text: this.text,
                    author: this.author
                },
                method: "POST"
            })
                .then(res => {
                    if (res.status === 200) {
                        this.$router.push("/");
                    } else {
                        this.$store.commit(
                            "setErrorMessage",
                            "Something went wrong!"
                        );
                        this.$store.commit("setErrors", []);
                    }
                })
                .catch(err => {
                    let errors = err.response.data.errors
                        ? err.response.data.errors
                        : [];
                    this.$store.commit(
                        "setErrorMessage",
                        err.response.data.message
                    );
                    this.$store.commit("setErrors", errors);
                });
        }
    }
};
</script>
<style scoped>
form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

input {
    padding: 0.25rem;
    margin-bottom: 0.5rem;
}
textarea {
    padding: 0.25rem;
    width: 100%;
    font-family: "Muli", sans-serif;
    font-size: 14px;
    margin-bottom: 0.5rem;
}

button {
    margin-top: 0.5rem;
    background-color: rgb(223, 223, 223);
    padding: 0.5rem 1rem;
    border: 1px solid grey;
    font-size: 13px;
    border-radius: 5px;
    cursor: pointer;
}
</style>
